.panel-col {
  display: flex;
  flex-direction: row;
  &.panel-col-2--5-0 {
    .pane-col1{
      width: 50%;
    }
  }
  &.panel-col-2--5-5 {
    .pane-col{
      width: 50%;
    }
  }
  &.panel-col-3--3-3-3 {
    display: flex;
    flex-direction: row;
    .pane-col{
      flex: 1;
      flex-basis: 33.333333%;
    }
  }
}
